@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main_container {
	background-color: white;
	font-family: 'Anton', sans-serif;
	-ms-user-select: none;
	-webkit-user-select: none;
	user-select: none;
}
.header {
	display: block;
	height: 30%;
	text-align: center;
}
.body {
	display: block;
	text-align: center;
}
.footer {
	display: block;
	text-align: center;
	border-top: dotted 3px #ababab;
}
.logo_container {
	padding-top: 50px;
	position: relative;
}
.rotate_img {
	-webkit-animation: rotate 40s linear infinite;
	        animation: rotate 40s linear infinite;
	transform-origin: 50% 50%;
}
@-webkit-keyframes rotate {
	100% {
		transform: rotate(360deg);
	}
}
@keyframes rotate {
	100% {
		transform: rotate(360deg);
	}
}
@-webkit-keyframes rotate_rev {
	100% {
		transform: rotate(-360deg);
	}
}
@keyframes rotate_rev {
	100% {
		transform: rotate(-360deg);
	}
}
.rotate_gear {
	-webkit-animation: rotate 20s linear infinite;
	        animation: rotate 20s linear infinite;
	transform-origin: 50% 50%;
}
.rotate_gear_small {
	-webkit-animation: rotate_rev 16s linear infinite;
	        animation: rotate_rev 16s linear infinite;
	transform-origin: 50% 50%;
}

.auth_container {
	padding-top: 40px;
}
.subTitle {
	font-weight: 600;
	font-size: 20px;
	color: #333333;
}
.author {
	font-weight: 600;
	font-size: 15px;
	color: #888888;
}
.link_tit {
	padding-top: 5px;
	color: black;
	text-decoration: none;
}
.banner_container {
	padding-top: 20px;
	padding-bottom: 30px;
}
.banner {
	font-family: 'Dancing Script', cursive;
	font-size: 40px;
	color: #333333;
}

.category {
	display: flex;
	justify-content: center;
	font-size: 14px;
	font-weight: 500;
}
.tag {
	padding-right: 20px;
	padding-left: 20px;
}

